<template>
    <div class="pb-4 border-b-2">
        <button @click="toggleAccordion()" class="flex justify-between items-center w-full space-x-5 "
            :aria-expanded="isOpen" :aria-controls="`collapse${_uid}`">
            <slot name="title" />
            <span><svg class="w-3 transition-all duration-200 transform" :class="{
                'rotate-180': isOpen,
                'rotate-0': !isOpen,
            }" fill="none" stroke="currentColor" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 10"
                    aria-hidden="true">
                    <path d="M15 1.2l-7 7-7-7" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" />
                </svg></span>

        </button>

        <div class="mt-10" v-show="isOpen" :id="`collapse${_uid}`">
            <slot name="content" />
        </div>
    </div>
</template>
  
<script>
export default {
    props: ['value'],
    data() {
        return {
            isOpen: this.value,
        };
    },

    methods: {
        toggleAccordion() {
            this.isOpen = !this.isOpen;
        },
    },
};
</script>